import styled from '@emotion/styled';
import { useMemo, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import {
  type ProductDetailRequestParams,
  useGetProductDetail,
} from '@/api/hooks/useGetProductDetail';
import { useGetProductOptions } from '@/api/hooks/useGetProductOptions';
import { Button } from '@/components/common/Button';
import { useAuth } from '@/provider/Auth';
import { getDynamicPath, RouterPath } from '@/routes/path';
import { orderHistorySessionStorage } from '@/utils/storage';

import { CountOptionItem } from './OptionItem/CountOptionItem';
import { getBaseUrl } from '@/api/instance';

type Props = ProductDetailRequestParams;

type CountState = {
  [key: string]: string;
};

export const OptionSection = ({ productId }: Props) => {
  const { data: detail } = useGetProductDetail({ productId });
  const { data: options } = useGetProductOptions({ productId });

  const [counts, setCounts] = useState<CountState>(() => {
    const initialCounts: CountState = {};
    if (options) {
      options.forEach(option => {
        initialCounts[option.id] = '0';
      });
    }
    return initialCounts;
  });

  useEffect(() => {
    if (options) {
      setCounts(prevCounts => {
        const newCounts = { ...prevCounts };
        options.forEach(option => {
          if (!newCounts[option.id]) {
            newCounts[option.id] = '0';
          }
        });
        return newCounts;
      });
    }
  }, [options]);

  const totalPrice = useMemo(() => {
    if (!detail) return 0;
    const count = Object.values(counts).reduce((acc, countStr) => acc + Number(countStr), 0);
    return detail.price * count;
  }, [detail, counts]);

  const navigate = useNavigate();
  const authInfo = useAuth();

  const handleInterestClick = async () => {
    if (!authInfo) {
      const isConfirm = window.confirm(
        '로그인이 필요한 메뉴입니다.\n로그인 페이지로 이동하시겠습니까?',
      );

      if (!isConfirm) return;
      return navigate(getDynamicPath.login());
    }

    try {
      const requestBody = {
        productId: parseInt(productId, 10),
        quantity: null,
      };

      console.log('Sending request to add wish', requestBody); // 잘 받아오는지 확인 - 추후 삭제 예정

      const response = await fetch(`${getBaseUrl()}/api/wishes`, {
        method: 'POST',
        headers: {
          Authorization: `Bearer ${authInfo.token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(requestBody),
      });

      if (response.ok) {
        alert('관심 등록 완료');
      } else if (response.status === 400) {
        alert('잘못된 요청입니다.');
      } else if (response.status === 404) {
        alert('회원 또는 상품을 찾을 수 없습니다.');
      } else if (response.status === 401) {
        alert('토큰이 유효하지 않습니다. 다시 로그인해주세요.');
      } else {
        alert('관심 등록에 실패했습니다.');
      }
    } catch (error) {
      console.error('관심 등록 에러:', error);
      alert('관심 등록 에러가 발생했습니다.');
    }
  };

  const handleOrderClick = () => {
    if (!authInfo) {
      const isConfirm = window.confirm(
        '로그인이 필요한 메뉴입니다.\n로그인 페이지로 이동하시겠습니까?',
      );

      if (!isConfirm) return;
      return navigate(getDynamicPath.login());
    }

    const totalCount = Object.values(counts).reduce((acc, countStr) => acc + Number(countStr), 0);

    if (totalCount === 0) {
      alert('옵션을 선택해주세요.');
      return;
    }

    const selectedOptions = options
      .filter(option => Number(counts[option.id]) > 0)
      .map(option => ({
        productId: parseInt(productId, 10),
        optionId: option.id,
        quantity: Number(counts[option.id]),
        message: '',
      }));

    console.log('선택된 옵션 및 수량:', selectedOptions); // 콘솔 로그 추가

    orderHistorySessionStorage.set(selectedOptions);

    navigate(RouterPath.order);
  };

  return (
    <Wrapper>
      {options && options.map(option => (
        <CountOptionItem
          key={option.id}
          name={option.name}
          value={counts[option.id]}
          onChange={value => setCounts(prev => ({ ...prev, [option.id]: value }))}
        />
      ))}
      <BottomWrapper>
        <Button theme="kakao" size="large" onClick={handleInterestClick}>
          관심 등록
        </Button>
        <PricingWrapper>
          총 결제 금액 <span>{totalPrice}원</span>
        </PricingWrapper>
        <Button theme="black" size="large" onClick={handleOrderClick}>
          나에게 선물하기
        </Button>
      </BottomWrapper>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  padding: 30px 12px 30px 30px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const BottomWrapper = styled.div`
  padding: 270px 0 0;
`;

const PricingWrapper = styled.div`
  margin-bottom: 20px;
  padding: 18px 20px;
  border-radius: 4px;
  background-color: #f5f5f5;
  display: flex;
  justify-content: space-between;

  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  color: #111;

  & span {
    font-size: 20px;
    letter-spacing: -0.02em;
  }
`;